/*--- Pricing Table: Style 1 ---*/

.eb-pricing.style-1 {
	position: relative;
	z-index: 0;
	text-align: center;
}

.eb-pricing.style-1 .eb-pricing-item {
	border: 1px solid rgba(9, 9, 9, 0.1);
